.app {
    font-family: 'Poppins', sans-serif;
    line-height: 1.5;
    color: var(--clr-fg);
    background-color: var(--clr-bg);
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.page {
    flex-grow: 1;
}

/* Original
.light {
  --clr-bg: #fcfcfc;
  --clr-bg-alt: #fff;
  --clr-fg: #555;
  --clr-fg-alt: #444;
  --clr-primary: #2978b5;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
*/

.light {
    --clr-bg: #fcfcfc;
    --clr-bg-alt: #fff;
    --clr-fg: #555;
    --clr-fg-alt: #444;
    --clr-primary: #2978b5;
    --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

/* Original
.dark {
  --clr-bg: #23283e; 
  --clr-bg-alt: #2a2f4c;
  --clr-fg: #bdbddd;
  --clr-fg-alt: #cdcdff;
  --clr-primary: #90a0d9;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
*/

.dark {
    --clr-bg: #1e1e1e; 
    --clr-bg-alt: #252526;
    --clr-fg: #dddddd;
    --clr-fg-alt: #eeeeee;
    --clr-primary: #2978b5;
    --shadow: rgba(100, 100, 111, 0.2) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

main {
    max-width: 1100px;
    width: 95%;
    margin: 0 auto;
}

.section {
    margin-top: 0em;
}

.section__title {
    text-align: left;
    margin-top: 2em;
    margin-bottom: 1em;
}

.center {
    display: flex;
    align-items: center;
}

.link {
    color: var(--clr-primary);
    padding: 0 0 0.2em 0;
    position: relative;
}

.link:hover {
    color: var(--clr-primary);
}

.link::before {
    content: '';
    display: inline;
    width: 0%;
    height: 0.2em;
    position: absolute;
    bottom: 0;
    background-color: var(--clr-primary);
    transition: width 0.2s ease-in;
}

.link:hover::before,
.link:focus::before {
    width: 100%;
}

.link--nav {
    color: var(--clr-fg);
    text-transform: lowercase;
    font-weight: 500;
}

.link--icon {
    color: var(--clr-fg);
}

.btn {
    display: block;
    cursor: pointer;
    padding: 0.8em 1.4em;
    font-weight: 500;
    font-size: 0.9rem;
    text-transform: lowercase;
    transition: transform 0.2s ease-in-out;
}

.btn--outline {
    color: var(--clr-primary);
    border: 2px solid var(--clr-primary);
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.btn--outline:hover,
.btn--outline:focus {
    color: var(--clr-bg);
}

.btn--outline:before {
    content: '';
    position: absolute;
    background-color: var(--clr-primary);
    right: 100%;
    bottom: 0;
    left: 0;
    top: 0;
    z-index: -1;
    transition: right 0.2s ease-in-out;
}

.btn--outline:hover:before,
.btn--outline:focus:before {
    right: 0;
}

.btn--plain {
    text-transform: initial;
    background-color: var(--clr-bg-alt);
    box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
    border: 0;
}

.btn--plain:hover {
    transform: translateY(-4px);
}

.btn--icon {
  padding: 0;
}

.btn--icon:hover,
.btn--icon:focus {
    color: var(--clr-primary);
}

.btn--icon:active {
    transform: translateY(-5px);
}

@media (max-width: 900px) {
    .section {
        margin-left: 1px;
        margin-right: 1px;
    }
}
